import logo from './logo.svg';
import './App.css';
import kk from './all.json';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import { CardDeck } from 'react-bootstrap';
import { GridListTile, GridList,Grid, Accordion, AccordionSummary, AccordionDetails, List, ListItem, ListItemText, ListItemSecondaryAction, IconButton } from '@material-ui/core';
import Flatlist from 'flatlist-react';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';

const useStyles = makeStyles((theme) =>({
  root: {
    width: 345,
    margin: 10,
  },
  media: {
    height: 140,
  },
  gridList: {
    width: 500,
    height: 450,
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
    width: 230,
  },
  bb:{
    width: '100%',
  }
}));
function Showw(props){
  const classes = useStyles();
return(
  <Card className={classes.root}>
  <CardActionArea>
    <CardMedia
      className={classes.media}
      image={props.value.image}
      title={props.value.title}
    />
    <CardContent>
      <Typography gutterBottom variant="h5" component="h2">
      {props.value.title}
      </Typography>
      <Typography variant="body2" color="textSecondary" component="p">
      {props.value.about}
      </Typography>
    </CardContent>
  </CardActionArea>
  <CardActions>
    <Button size="small" color="primary">
      Share
    </Button>
    <Button size="small" color="primary">
      Learn More
    </Button>
  </CardActions>
  <CardActions>
    <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography className={classes.heading}>Show links</Typography>
        </AccordionSummary>
        <AccordionDetails>
        <div className={classes.bb}>
          {props.value.seasons.map((v,i) =>{
            return <Accordion>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon />}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <Typography className={classes.heading}>{"Season "+(i+1)}</Typography>
            </AccordionSummary>
            <AccordionDetails>
          
            <List className={classes.heading}>
            {v.episodes.map((value, index) => {
          return <ListItem>
             <ListItemText
                    primary={"Episode"+(index+1)}
                    secondary={"test"}
                  />
                  <ListItemSecondaryAction>
                    <IconButton edge="end" aria-label="play">
                      <PlayArrowIcon />
                    </IconButton>
                  </ListItemSecondaryAction>
          </ListItem>
        })}
            </List>
           
            </AccordionDetails>
        </Accordion>
          })}
        </div>
        </AccordionDetails>
    </Accordion>
  </CardActions>
  
</Card>
);
}
function ShowCards(props){
  return( 
<Grid container >
          {props.value.map((value, index) => {
          return <Showw value={value}/>
        })}    </Grid>
    );
}
function App() {
  const classes = useStyles();
  const data = JSON.parse(JSON.stringify(kk));
  return (
    <div className="App">
      <header className="App-header">
      </header>
      <body className="body">
      
      <ShowCards className="cards" value={data}/>
      </body>
    </div>
  );
}

export default App;
